* {
  padding: 0;
  margin: 0;
  list-style: none;
}

img {
    display: block;
    width: 100%;
    height: auto;
}


:root {
    --main-bg-color:#2f2fa2;
    --main-active-color:#f64c72;
    --main-text-color:#00ffff;
    --secondary-bg-color:#9898a0;
    --box-shadow: 0px 0px 24px 0px #75FF4D;
  }

.App {
    background-color: var(--secondary-bg-color);
    padding: 10px;
}

.nav-menu {
    display: flex;
    justify-content: space-around;
    background-color: var(--main-bg-color);
    margin-bottom: 3px;
}

.nav-menu a {
    padding: 20px 60px;
    text-decoration: none;
    color: var(--main-text-color);
}

.nav-link:hover  {
    background-color: var(--main-active-color);

}


.magazine-main{
    display: flex;
    color: var(--main-text-color);
    background-color: var(--secondary-bg-color);
}

.filter-bar{
    width: 15%;
    cursor: pointer;
    text-align: center;
}

.menu-title , .menu-items , .menu-items-active {
    padding: 7px;
    background-color: var(--main-bg-color);
}

.menu-items  {
    background-color: var(--main-bg-color);
    border-bottom: 5px solid transparent;
}

.menu-items-active {
    background-color: var(--main-bg-color);
    border-bottom: 5px solid var(--main-text-color);
}





.menu-title:hover {
    background-color: var(--main-active-color);
}

.menu-items:hover {
    background-color: var(--main-active-color);
}


.menu-items-active {
    background-color: var(--main-active-color);
}

.product-container {
    display: grid;
    grid-template-columns: repeat(4 , 1fr);
    grid-gap: 20px ;
    margin-top: 15px;
}

.search-and-products-container {
    padding: 20px;
}


#input-for-diapason-max {
    position: absolute ;
      top: 0px;
    left :0 ;
  }

.price-range {
    width: 40%;
    display: inline-block;
    margin-left: 20px;
}

.paginator{
    display: block;
    text-align: center;
    margin-top: 20px;
    padding: 20px;
}

.product-item {
   margin: auto;
   box-shadow: var(--box-shadow) ;
   width: 230px;
   overflow: hidden;
   height: 510px;
   background-color: var(--secondary-bg-color);
}

.product-page {
    margin: auto ;
    padding: 30px;
    box-shadow: var(--box-shadow) ;
    width: 600px;
    height: auto;
    color: var(--main-text-color);
    background-color: var(--secondary-bg-color);
 }

li {
    padding-left: 5px;
}